.offers {
    display: flex;
    align-items: center;
}

.offers-color {
    background: rgba(243, 245, 244, 1);

    .offers-left {
        &__wrapper {
            display: flex;
            align-items: center;
            padding-bottom: 40px;

            @include mediaTablet {
                padding-bottom: 30px;
            }
        }

        &__item {
            &-left {
                &--img {
                    width: 50px;
                    height: 50px;
                    margin-right: 8px;
                }
            }

            &-right {
                &--title {
                    font-size: 18px;
                    font-weight: 700;
                    color: rgba(24, 18, 30, 1);
                    margin-bottom: 4px;
                }

                &--subtitle {
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 1);
                }
            }
        }
    }

    .offers-right {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;

        @include mediaTablet {
            display: none;
         
        }

        &__img {
            width: 225px;
            height: 225px;

            @include mediaTablet {
                
            }
        }
    }
}

.img {
    @include mediaTablet {
      width: 50%;          
    }
    
}